  .menu-toggle {
    min-height: 24px;
    min-width: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    fill: #2C2C2C;
    margin: 0;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    border: none;
    border-radius: 50%;
    outline: none;
  }
.menu-burger{
  fill: #7EB101;
}


  
  @media screen and (min-width: 768px) {
    .menu-toggle {
      display: none;
    }
  }
  @media screen and (max-width: 767px) {
    .mobile-menu {
      display: none;
    }
  }

  
  .menu-toggle:hover,
  .menu-toggle:focus {

    fill: #FFE550;
  }
  
  .menu-container {
       position: fixed;
      top: 50px;
      // left: 0;
      right: 0;
    width: 140px;
    height: 255px;
    padding-left: 16px;
    padding-top: 16px;

    

    background-color: #fff;
    z-index: 999;  
    transform: translateX(100%);
    
    box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);
    border-radius: 30px 0px 30px 30px;



    transition: transform 250ms ease-in-out;
  }
  
  .menu-container.is-open {
    transform: translateX(0);
  }
  
  .menu-close {
    position: absolute;
    top: 4px;
    right: 4px;
    // color: #fff;
  }
  
  .mobile-menu {
 
    padding-right: 10px;
    margin: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;   
    padding-left: 0;

   
    font-weight: 400;
    font-size: 16px;
    line-height: 1.18;
    


    // letter-spacing: 0.02em;
    
    color: #000;


  }
  
  .mobile-menu .link {
    display: block;

    color: #212121;
    text-decoration: none;
  }

